.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 11;
  background: rgba(0, 0, 0, 0.4);
  cursor: auto;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 12;
  overflow: hidden;
  background-color: white;
  margin: auto;
  animation: appear 0.2s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;

  width: 100%;
  height: 100%;
  max-width: 200px;
  max-height: 245px;
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.modal>div {
  width: 100%;
  height: 100%;
  overflow: auto;
  text-align: left;
}

.modal>div::-webkit-scrollbar {
  width: 4px;
  border-radius: 10px;
}

.modal>div::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}

.modal>div::-webkit-scrollbar-thumb {
  background: #dcd7fe;
  border-radius: 10px;
}

.footer {
  text-align: right;
  margin-right: 3rem;
  margin-bottom: 1rem;
}